.navigation {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: var(--nav-bg);
    border-bottom: 1px solid transparent;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.navigation.scrolled {
    background: rgba(var(--nav-bg-rgb), 0.94);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-bottom-color: var(--border);
    box-shadow: 0 4px 24px var(--shadow);
}

.nav-container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 0.8rem 2.2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

/* Logo */
.nav-logo {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    text-decoration: none;
    color: var(--text-primary);
    font-family: "Space Grotesk", sans-serif;
    font-weight: 700;
    font-size: 1.5rem;
    letter-spacing: 0.1em;
    position: relative;
    z-index: 10;
    transition: all 0.3s ease;
}

.nav-logo:hover {
    color: var(--accent);
}

.nav-logo-image {
    width: 136px;
    height: auto;
    display: block;
    object-fit: contain;
}

.logo-hexagon {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo-text {
    position: relative;
    display: inline-flex;
    align-items: baseline;
    gap: 0.08em;
    text-transform: lowercase;
}

.logo-text::after {
    content: "";
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0;
    height: 2px;
    background: linear-gradient(90deg, var(--accent) 0%, var(--text-primary) 100%);
    transition: width 0.3s ease;
}

.nav-logo:hover .logo-text::after {
    width: 100%;
}

.logo-we {
    color: var(--accent);
    font-weight: 800;
}

.logo-hype {
    color: var(--text-primary);
    font-weight: 700;
}

.logo-dot {
    color: var(--accent);
    font-weight: 800;
}

/* Desktop Menu */
.nav-menu {
    display: flex;
    align-items: center;
    gap: 2.5rem;
    list-style: none;
    margin: 0;
    padding: 0;
}

.nav-menu li {
    position: relative;
}

.nav-link {
    position: relative;
    display: inline-block;
    color: var(--text-secondary);
    text-decoration: none;
    font-weight: 500;
    font-size: 0.95rem;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    padding: 0.5rem 0;
    transition: color 0.3s ease;
}

.nav-link:hover {
    color: var(--accent);
}

.nav-link.active {
    color: var(--accent);
    font-weight: 600;
}

.nav-link-text {
    position: relative;
    z-index: 1;
}

.nav-link-underline {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    background: var(--accent);
    display: block;
}

.nav-link.active .nav-link-underline {
    width: 100%;
}

/* Right Section */
.nav-right {
    display: flex;
    align-items: center;
    gap: 1rem;
    z-index: 10;
}

/* Mobile Menu Toggle */
.mobile-menu-toggle {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 32px;
    height: 24px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 1001;
    position: relative;
}

.hamburger-line {
    width: 100%;
    height: 2px;
    background: var(--text-primary);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    transform-origin: center;
}

.mobile-menu-toggle.open .hamburger-line:nth-child(1) {
    transform: translateY(11px) rotate(45deg);
}

.mobile-menu-toggle.open .hamburger-line:nth-child(2) {
    opacity: 0;
    transform: translateX(-20px);
}

.mobile-menu-toggle.open .hamburger-line:nth-child(3) {
    transform: translateY(-11px) rotate(-45deg);
}

/* Mobile Menu */
.mobile-menu-backdrop {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    z-index: 999;
}

.mobile-menu {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    max-width: 400px;
    background: var(--bg-primary);
    border-left: 1px solid var(--border);
    z-index: 1000;
    padding: 6rem 2rem 2rem;
    overflow-y: auto;
    box-shadow: -8px 0 32px var(--shadow);
}

.mobile-menu-decoration {
    position: absolute;
    top: 2rem;
    right: 2rem;
    opacity: 0.1;
    pointer-events: none;
}

.mobile-menu-decoration .decoration-hex-1 {
    position: absolute;
    top: 0;
    right: 0;
}

.mobile-menu-decoration .decoration-hex-2 {
    position: absolute;
    top: 60px;
    right: 40px;
}

.mobile-menu-decoration .decoration-hex-3 {
    position: absolute;
    top: 120px;
    right: 80px;
}

.mobile-nav-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.mobile-nav-link {
    display: block;
    position: relative;
    padding: 1.5rem 0;
    text-decoration: none;
    color: var(--text-secondary);
    border-bottom: 1px solid var(--border);
    transition: all 0.3s ease;
}

.mobile-nav-link:hover {
    color: var(--accent);
    padding-left: 1rem;
}

.mobile-nav-link.active {
    color: var(--accent);
}

.mobile-nav-number {
    display: inline-block;
    font-size: 0.75rem;
    font-weight: 600;
    color: var(--text-tertiary);
    margin-right: 1rem;
    font-family: "Space Grotesk", sans-serif;
}

.mobile-nav-text {
    font-size: 1.5rem;
    font-weight: 600;
    font-family: "Space Grotesk", sans-serif;
    letter-spacing: 0.05em;
    text-transform: uppercase;
}

.mobile-nav-line {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    background: var(--accent);
}

.mobile-menu-footer {
    margin-top: 4rem;
    padding-top: 2rem;
    border-top: 1px solid var(--border);
}

.mobile-menu-tagline {
    font-size: 1.25rem;
    font-family: "Space Grotesk", sans-serif;
    font-weight: 500;
    color: var(--text-tertiary);
    font-style: italic;
    letter-spacing: 0.02em;
}

/* Decorative Lines */
.nav-line-left,
.nav-line-right {
    position: absolute;
    bottom: 0;
    height: 1px;
    background: var(--border);
    opacity: 0.5;
}

.nav-line-left {
    left: 0;
    width: 40%;
}

.nav-line-right {
    right: 0;
    width: 40%;
}

/* Responsive */
@media (max-width: 1024px) {
    .nav-container {
        padding: 0.7rem 1.8rem;
    }

    .nav-menu {
        gap: 1.8rem;
    }

    .nav-link {
        font-size: 0.9rem;
    }
}

@media (max-width: 768px) {
    .nav-container {
        padding: 0.6rem 1.2rem;
    }

    .desktop-menu {
        display: none;
    }

    .mobile-menu-toggle {
        display: flex;
    }

    .nav-logo-image {
        width: 122px;
    }

    .mobile-menu {
        max-width: 100%;
    }

    .nav-line-left,
    .nav-line-right {
        display: none;
    }
}

@media (max-width: 480px) {
    .nav-container {
        padding: 0.55rem 1rem;
    }

    .nav-logo-image {
        width: 108px;
    }

    .mobile-nav-text {
        font-size: 1.25rem;
    }

    .mobile-menu {
        padding: 5rem 1.5rem 1.5rem;
    }
}

/* Smooth scroll behavior */
html {
    scroll-behavior: smooth;
}

/* Focus styles for accessibility */
.nav-link:focus-visible,
.mobile-nav-link:focus-visible,
.mobile-menu-toggle:focus-visible {
    outline: 2px solid var(--accent);
    outline-offset: 4px;
    border-radius: 2px;
}
